.first_div h3{
    font-size: 24px;
    font-weight: 300;
    /* margin: 0 initial 0 initial; */
    letter-spacing: 1px;
    padding-top:0px;
    margin-top: 0px;
    margin-bottom: 40px;
    font-weight:200 ;
    color: #93959f;
    line-height: 40px;
}
.second_div{
    width: 45vw;
    background: url(/static/media/food.7a07461e.jpeg);
    background-size:cover;
}
.searchDiv{
    height: 58px;
    width: 100%;
    display: flex;
    /* border: 1px solid var(--swiggy_color); */
    margin-bottom: 30px;
}

.searchDiv input{
    height: 100%;
    flex: 2 1;
    border: none;
    padding-left: 1rem ;
    font-size: 17px;
    color: #7e808c;
}
.searchDiv input:focus{
    outline: none;
}


#locateBtn{
    height: 100%;
    border: none;
    flex: 1 1;
    background: none;
    cursor: pointer;
    color: #535665;
}
#locateBtn:hover{
    background-color: #eaebf1;
    color: black;
}
#input{
    flex:3 1;
    display: flex;
    border: 1px solid #c2c3c7;
} 
#input:focus-within{
    border: 1px solid #fc8019!important;
    border: 1px solid var(--swiggy_color)!important;

}
#findBtn{
    height:100%;
    border: none;
    background-color: #fc8019;
    background-color: var(--swiggy_color) ;
    color: #fff;
    flex: 1 1;
    font-size: 17px;
    font-weight: 700;
    cursor: pointer;
}

.first_div p{
    color: #a9abb2;
    letter-spacing: 1px;
    font-weight: 600;
}

p span{
    /* color: #686b78; */
    color: #282c3f;
    font-weight: 600;
}

.logo_container img:hover{
    -webkit-filter: drop-shadow(0px 0px 6px #eec6a6);
            filter: drop-shadow(0px 0px 6px #eec6a6);
}
.features{
    background: #2b1e16;
    display: flex;
    justify-content: space-around;
    padding: 0 0 4% 0;
   
}
.feature_container{
    width: 16vw;
    text-align: center;
    
}
.feature_container h3{
    color: #fff;
    font-weight: 700;
}
.feature_container p{
    color: #e0cbc1;
    line-height: 1.3;
}
.feature_container img{
    width: 7rem;
    
}
#fast_delivery{
    padding-top: 1.8%;
}
.restaurants{
    padding: 0 7vw;
    display: flex;
}
.restaurants h2{
    font-size: 40px;
    font-weight: 600;
    line-height: 1;
    width: 30vw;
    letter-spacing: -.45px;
    color: #282c3f;
}
.restaurants p{
    font-size: 20px;
    font-weight: 300;
    width: 33vw;
    color: #7e808c;
    line-height: 1.2;
}
.left_stuff{
    padding: 3rem 7rem 1.5rem 0;
}
.phone_container{
    display: flex;
}
.phone_container img{
    width: 25vw;
}
.downbutton_container{
    display: flex;
    margin: 15% 0 5% 2% ;
}
.downbutton_container .download_buttons{
    background: none;
    border: none;
}
.download_buttons img{
    width: 13vw;
}
footer{
    background: #000;
    color: #fff;
    padding:4% 8vw;
}
footer hr{
    color: gray;
}
footer h4{
    color: gray;
}
footer p:hover{
    text-shadow: 0px 0px 15px #fff;
}
#about{
    display: flex;
}
#about div{
    width: 20vw;
}
.downlogo{
    text-align: center;
}
.downlogo img{
    height: 4rem;
    border: 1px solid #fff;
    border-radius: 15px;
    margin:1.5rem 0;
    cursor:pointer;
}
.flexed_cities{
    display: flex;
    justify-content:space-between;
}
.flexed_cities div{
    text-transform: capitalize;
    font-size: 17px;
    line-height: 1.2;
}
#logos{
    padding-top: 2%;
    display:flex;
    justify-content: space-between;
}
#logos img{
    width: auto;
    height: 100%;
}
#logos p{
    font-size: 20px;
}
#sociallogos{
    display: flex;
    width: 13vw;
    justify-content: space-between;
}
#sociallogos svg{
    font-size: 30px;
    margin-top:1rem;
}
#logos img:hover{
    -webkit-filter:drop-shadow(0px 0px 5px #777);
            filter:drop-shadow(0px 0px 5px #777);
}
.downlogo img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
